import { getTokenF, setTokenF, removeTokenF } from "@/utils/token";
import { createSlice } from "@reduxjs/toolkit";

const userStore = createSlice({
    name: 'user',
    initialState: {
        token: getTokenF() || ''
    },
    reducers: {
        setToken(state, action) {
            state.token = action.payload
            // localStorage
            setTokenF(action.payload)

        },
        clearToken(state, action) {
            state.token = ''
            removeTokenF()
        }

    }
})
//actionCreater
export const { setToken, clearToken } = userStore.actions

export default userStore.reducer