import { createHashRouter ,Navigate } from 'react-router-dom'
import { lazy, Suspense } from 'react'
import NotFound from '@/views/NotFound'
const Login = lazy(() => import('@/views/Login/Login'))
const LayoutBox = lazy(() => import('@/views/Layout/LayoutBox'))
const PageIndex = lazy(() => import('@/views/PageIndex/PageIndex'))
const About = lazy(() => import('@/views/About/About'))
const FormBaseView = lazy(() => import('@/views/FormView/FormBaseView/FormBaseView'))
const router = createHashRouter([
   {
      path: '/login',
      element: <Suspense fallback={''}><Login /></Suspense>
   },
   {
      path: '/',
      element: <Suspense fallback={''}><LayoutBox /></Suspense>,
      children: [
         {
            index:true,
            element:<Navigate to="/index"></Navigate>
         },
         {
      
            path: 'index',
            element: <Suspense fallback={''}><PageIndex /></Suspense>,
         },
         {
            path: 'about',
            element: <Suspense fallback={''}><About /></Suspense>,

         },
         {
            path: '/form/base-form',
            element: <Suspense fallback={''}><FormBaseView /></Suspense>,
         }
      ]
   },

   {
      path: '*',
      element: <NotFound />
   }
])

export default router